<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">{{ monthText }}</h2>
      <div class="d-flex justify-content-between align-items-center mb-3">
        <div class="text-muted">Month End Closing Status</div>
      </div>

      <br /><br />
      <!-- 마감 체크박스 -->
      <div class="form-check">
        <input class="form-check-input" type="checkbox" id="monthCloseCheck" v-model="isMonthCloseChecked" @change="handleCheckboxChange">
        <label class="form-check-label" for="monthCloseCheck">
          {{ monthCloseLabel }}
        </label>
      </div>

      <!-- 알림 메시지 -->
      <p class="text-danger mt-2">
        마감을 완료하고 되돌릴려면 결재권자의 권한이 필요합니다.<br />
        최종 마감의 다음달을 보여줍니다.
      </p>

      <!-- 마감 버튼 -->
      <button class="btn btn-primary mt-3" :disabled="!isButtonEnabled" @click="confirmationModalVisible = true">마감</button>
    </div>

    <AlertModal 
      :isVisible="confirmationModalVisible" 
      :disableBackgroundClose="true"
      title="마감 확인" 
      confirmText="확인" 
      cancelText="취소"
      @close="confirmationModalVisible = false" 
      @confirm="closureMonth"
      class="custom-alert-modal">
      <template #body>
        <p>정말로 마감하시겠습니까?</p>
      </template>
    </AlertModal>

    <AlertModal 
      :isVisible="resultModalVisible" 
      :disableBackgroundClose="true"
      :title="closureResultTitle" 
      confirmText="확인" 
      @confirm="resultModalVisible = false" 
      class="custom-alert-modal">
      <template #body>
        <p>{{ closureResultBody }}</p>
      </template>
    </AlertModal>

  </div>
</template>

<script setup>
import { ref } from 'vue';
import AlertModal from '@/components/modal/AlertModal.vue';

const today = new Date();
let currentYear = today.getFullYear();
let currentMonth = today.getMonth() + 1; // 월은 0부터 시작하므로 +1 필요

// 현재 월이 1월이면 이전 달은 12월, 년도는 작년으로 변경
if (currentMonth === 1) {
  currentMonth = 12;
  currentYear -= 1; // 년도를 작년으로 변경
} else {
  currentMonth -= 1; // 현재 월에서 -1 하여 이전 달로 변경
}

const confirmationModalVisible = ref(false);
const resultModalVisible = ref(false);
const closureResultTitle = ref('');
const closureResultBody = ref('');

const monthText = ref(`${currentMonth}월 마감`);
const monthCloseLabel = ref(`${currentYear}년 ${monthText.value} 진행`);

const isMonthCloseChecked = ref(false);
const isButtonEnabled = ref(false);

const handleCheckboxChange = () => {
  if (isMonthCloseChecked.value) {
    const currentDay = today.getDate();
    if (currentDay >= 1 && currentDay <= 31) {
      isButtonEnabled.value = true;
    } else {
      isButtonEnabled.value = false;
      alert('마감 버튼은 매월 1일부터 31일 사이에만 활성화됩니다.');
      isMonthCloseChecked.value = false;
    }
  } else {
    isButtonEnabled.value = false;
  }
};

const closureMonth = async () => {
  confirmationModalVisible.value = false;
  const hasRejected = true; // 실제 데이터 로직으로 교체해야 함
  if (hasRejected) {
    closureResultTitle.value = '확인 필요';
    closureResultBody.value = '결재가 되지 않은 건수가 있습니다.';
  } else {
    // 실제 마감 로직 추가
    closureResultTitle.value = '마감 완료';
    closureResultBody.value = '마감이 완료되었습니다.';
  }
  resultModalVisible.value = true;
}
</script>

